*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body{
  background-color: rgb(11, 30, 75);
  padding: 20px;
}
main{
  max-width: 800px;
  margin: 0 auto;
  border-radius: 10px;
  min-height: 400px;
  background-color: rgb(251, 249, 220);
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  padding: 20px;
}

.title {
  font-size: 40px;
}

.instructions {
  font-family: 'Inter', sans-serif;
  font-weight: 400;
  text-align: center;
  margin: 10px;
}

.dice-container{
  display: grid;
  grid-template: auto auto / repeat(5, 1fr);
  gap: 10px;
  margin: 10px 0 15px 0;
}

.die-face{
  height: 50px;
  width: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 2px 2px 5px black;
  border-radius: 8px;
  cursor: pointer;
}

.roll-dice{
  background-color: #503eff;
  color: white;
  width: 150px;
  height: 50px;
  font-size: 1.2rem;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.roll-dice:focus{
  outline: none;
}
.roll-dice:active{
  box-shadow: inset 5px 5px 10px -3px rgba(0, 0, 0, 0.7);
}

.die-num{
  display: grid;
  gap: 10px 4px;
  grid-template: repeat(2, 1fr) / repeat(3, 1fr);
}

.die-num div{
  width: 10px;
  height: 10px;
  background-color: red;
  border-radius: 10px;
  /* margin: 0 auto; */
}

.die-num:has(.five):not(:has(.six)){
  grid-template: repeat(2, 1fr) / repeat(2, 1fr);
  gap: 1px 10px;
}

.die-num:has(.five):not(:has(.six)) .three{
  grid-column: span 2;
  margin: 0 auto;
}

.die-num:has(.four):not(:has(.five)){
  grid-template: repeat(2, 1fr) / repeat(2, 1fr);
  gap: 10px;
}

.die-num:has(.three):not(:has(.four)){
  grid-template: repeat(3, 1fr) / repeat(1, 1fr);
  gap: 2px;
}

.die-num:has(.three):not(:has(.four)) .two{
  grid-column: 2;
  grid-row: 2;
}
.die-num:has(.three):not(:has(.four)) .three{
  grid-column: 3;
  grid-row: 3;
}

.die-num:has(.two):not(:has(.three)){
  grid-template: repeat(2, 1fr) / repeat(2, 1fr);
  gap: 2px;
}
.die-num:has(.two):not(:has(.three)) .one{
  grid-column: 2;
  /* grid-row: 1; */
}
.die-num:has(.two):not(:has(.three)) .two{
  grid-column: 1;
}
.die-num:has(.one):not(:has(.two)) {
  grid-template: 1fr / 1fr;
}